import './workOrder.less'
import bg from './img/work-bg.svg'
import discord from './img/discord.svg'
export default function WorkOrder({ lang }) {
  return (
    <div className='work-order-box'>
      <div className='work-logo'>
        <img src={bg} alt='' />
      </div>
      <div className='work-info'>
        <div className='work-title'>
          {lang['title']}
        </div>
        <div className='work-describe'>
          {lang['describe-one']} <a rel='noreferrer' target="_blank" href='https://discord.gg/bdk6JutcWj'>{lang['support']}</a> {lang['describe-two']}
        </div>
        <div className='m-work-logo'>
          <img src={bg} alt='' />
        </div>
        <a rel='noreferrer' target="_blank" href='https://discord.com/invite/kb8ZbYgp8M'>
          <div className='go-discord'>
            {lang['join-us-on']}
            <img src={discord} alt='discord' />
          </div>
        </a>

      </div>
    </div>
  )
}